@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap');

/* VitroCore 폰트 */
@font-face {
  font-family: 'VitroCore';
  font-weight: normal;
  font-style: normal;
  src: url('https://cdn.jsdelivr.net/gh/webfontworld/vitro/VitroCore.woff2') format('woff2'),
       url('https://cdn.jsdelivr.net/gh/webfontworld/vitro/VitroCore.woff') format('woff');
  font-display: swap;
}

html,
body {
  background: #000;
  color: white;
  height: 100%;
  padding: 0;
  margin: 0;
  font-family: 'Nanum Gothic', 'VitroCore', sans-serif;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Nanum Gothic', sans-serif;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
}

a,
button {
  text-decoration: none;
}

ul {
  list-style: none;
  padding-left: 0px;
}

#root {
  height: 100%;
  background-color: black;
}

/* 스크롤 디자인 설정 */
body::-webkit-scrollbar {
  width: 8px;
  /* 스크롤바 너비 */
}

body::-webkit-scrollbar-thumb {
  height: 30%;
  /* 스크롤바 길이 */
  background: #a5a5a5;
  /* 스크롤바 색상 */
  border-radius: 10px;
}

body::-webkit-scrollbar-track {
  background-color: #1b1b1b;
  /*스크롤바 배경 색상*/
}